import SubNavHeader from '../../../components/shared/sub-nav-header/sub-nav-header.component'
import './social.style.scss'
import headerImg from '../../../assets/img/Illustration Social.svg'
import Footer from '../../../components/footer/footer.component'
import blueArrow from '../../../assets/img/ArrowBleu.svg'

import blueGrid from '../../../assets/img/BG-PB.svg'
import greyGrid from '../../../assets/img/BG-PG.svg'

const GridElement = ({title, blueGridVisible, greyGridVisible, content}) => {

    return (
        <div className='grid-element'>
            <h1 className='element-title'>
                {title}
                <span><img className='blueArrow' src={blueArrow} alt='blueArrow'/></span>
            </h1>
            <p className='content'>{content}</p>
            {blueGridVisible && (
                <img className='blueGrid' src={blueGrid} alt='blue grid' />
            )}
            {greyGridVisible && (
                <img className='greyGrid' src={greyGrid} alt='grey grid' />
            )}
        </div>
    )
}

const Social = () => {
    return (
        <div className='social'>
            <SubNavHeader 
            title="Une <span className='underline'>prsence en ligne</span> forte pour
            <br/> une <span className='blue'>visibilit maximale.</span>"
            citation="Les rseaux sociaux sont aujourd'hui incontournables pour une <br/>entreprise qui veut tre visible et toucher son public cible"
            subtitle="Grez vos rseaux comme un pro "
            subcontent="Les rseaux sociaux sont une <strong>plateforme efficace</strong> pour atteindre <strong>votre public cible</strong> et crer une relation de confiance avec lui. Cependant, maintenir une prsence active sur les rseaux sociaux peut tre une tche ardue et chronophage. C'est l que notre service de Social Media Management entre en jeu."
            img={headerImg}
            />


            <section className="social-grid">

                <GridElement title="Audit de rseaux sociaux" greyGridVisible={true} content="Nous effectuons un audit complet de vos rseaux sociaux existants pour comprendre ce qui fonctionne et ce qui peut tre amlior. Cela nous permet de crer une stratgie de gestion des rseaux sociaux sur mesure pour rpondre  vos besoins spcifiques." />

                <GridElement title="Suivi et analyse" content="Nous surveillons et analysons les performances de vos rseaux sociaux pour comprendre ce qui fonctionne et ce qui ne fonctionne pas. Nous ajustons constamment notre stratgie pour maximiser l'engagement et la croissance de vos rseaux sociaux.." />

                <GridElement title="Planification de contenu" content="Nous planifions et crons un contenu original et cratif qui met en valeur votre entreprise et vos produits. Nous nous assurons que chaque publication est en ligne avec votre image de marque et vos objectifs commerciaux." />

                <GridElement title="Gestion de communaut" blueGridVisible={true} content="Nous grons votre communaut sur les rseaux sociaux en rpondant  tous les commentaires et messages entrants. Nous nous engageons avec votre public pour crer une relation de confiance et de loyaut avec votre marque." />

                <GridElement title="Gestion discord" content="Avec des millions d'utilisateurs actifs chaque jour, Discord est l'endroit idal pour tablir une communaut en ligne. Nous vous aiderons  grer votre serveur Discord en crant une stratgie de communication efficace, en crant du contenu engageant et en modrant." />

                <GridElement title="Campagne publicitaire" content="Nous crons et grons des campagnes publicitaires cibles sur les rseaux sociaux pour maximiser votre retour sur investissement et atteindre un public plus large." />



            </section>

            <Footer />
        </div>
    )
}

export default Social