import './subcontent.style.scss'
import greyGrid from '../../../../assets/img/BG-PG.svg'
import blueGrid from '../../../../assets/img/BG-PB.svg'
import blueArrow from '../../../../assets/img/ArrowBleu.svg'
const SubContent = () => {
    return (
        <div className='veille-content'>
            <section className='veille-description'>   
                <img className='grey-grid' src={greyGrid} alt='grey grid'/>
                <h1 className='description-title'>Veille stratgique 
                    <span><img className='blueArrow' src={blueArrow} alt='blue arrow'/></span>
                </h1>
                <p className='description-content'>Nous effectuons une veille stratgique pour vous aider  comprendre les tendances mergentes et les 
                opportunits de croissance dans votre secteur d'activit. Nous vous aidons galement  laborer des stratgies efficaces pour rpondre 
                 ces tendances et opportunits.</p>
            </section>
            <section className='media'>
                    <h1 className='media-title'>Surveillance des mdias et de la concurrence 
                        <span><img className='blueArrow' src={blueArrow} alt='blue arrow'/></span>
                    </h1>
                <div className="media-content">
                    <div className='media-element'>
                        <h1 className='element-title'>Media 
                            <span><img className='blueArrow' src={blueArrow} alt='blue arrow'/></span>
                        </h1>
                        <p className='element-description'>Nous surveillons les mdias sociaux pour identifier les tendances 
                        mergentes dans votre secteur d'activit. Nous pouvons galement surveiller les mentions de votre 
                        entreprise et de vos concurrents pour vous aider  comprendre ce qui est dit  votre sujet.</p>
                    </div>
                    <div className='media-element'>
                        <h1 className='element-title'>Concurrence 
                            <span><img className='blueArrow' src={blueArrow} alt='blue arrow'/></span>
                        </h1>
                        <p className='element-description'>Nous surveillons la concurrence pour comprendre leurs stratgies et leurs activits. 
                        Nous effectuons une analyse comparative de votre entreprise par rapport  vos concurrents pour vous aider
                         comprendre comment vous vous situez sur le march.</p>
                        <img className='first-blue-grid' src={blueGrid} alt='blue-grid' />
                        <img className='second-blue-grid' src={blueGrid} alt='blue-grid' />
                    </div>
                </div>
            </section>
            <section className='analyse'>
                <img className='first-blue-grid' src={blueGrid} alt='blue-grid' />
                <img className='second-blue-grid' src={blueGrid} alt='blue-grid' />
                <h1 className='analyse-title'>Analyse et rapports 
                    <span><img className='blueArrow' src={blueArrow} alt='blue arrow'/></span>
                </h1>
                <p className='analyse-content'>Nous analysons les donnes pour identifier les tendances mergentes 
                et les opportunits de croissance dans votre secteur d'activit. Nous fournissons galement des rapports
                rguliers pour vous informer des dernires tendances et des dveloppements importants dans votre secteur
                d'activit. Ces rapports peuvent vous aider  prendre des dcisions claires et  rester au courant des 
                volutions de votre march.</p>
            </section>
        </div>
    )
}

export default SubContent